.footer {
  width: 100%;
  min-height: 200px;
  background: linear-gradient(135deg, rgba(45, 52, 75, 0.95) 0%, rgba(28, 34, 50, 0.98) 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px 20px;
  gap: 20px;
  text-align: center;
}

.socialMedia {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
}

.socialMedia svg {
  color: white;
  font-size: 40px;
  cursor: pointer;
  transition: transform 0.3s ease, color 0.3s ease;
}

.socialMedia svg:hover {
  transform: scale(1.2);
  color: #1da1f2; /* Optional: hover color (Twitter blue as example) */
}

.footer p {
  color: #ddd;
  font-size: 16px;
  margin: 0;
  opacity: 0.8;
}

/* Optional Responsive Tweak */
@media only screen and (max-width: 500px) {
  .socialMedia svg {
    font-size: 32px;
  }

  .footer p {
    font-size: 14px;
  }
}
