/* Ensure Font Awesome icons adapt to current theme and stay responsive */
.theme-toggle i,
.theme-toggle svg,
.mobile-menu-toggle i,
.mobile-menu-toggle svg,
.desktopLinks i,
.desktopLinks svg,
.mobile-menu-content i,
.mobile-menu-content svg {
  color: var(--text-color);
  width: 1em;
  height: 1em;
  font-size: 1.25rem;
  transition: color var(--transition-speed), transform var(--transition-speed);
}

