/* === CSS VARIABLES === */
:root {
  --primary-bg-color: #fefefe;
  --card-bg-color: #ffffff;
  --text-color: #333;
  --subtext-color: #555;
  --hover-shadow: rgba(0, 0, 0, 0.2);
  --transition-speed: 0.3s;
  --max-content-width: 1400px;
}

/* === MENU CONTAINER === */
.menu {
  width: 100%;
  padding: 4rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--primary-bg-color);
}

/* === MENU TITLE === */
.menu .menuTitle {
  font-family: 'Cursive', sans-serif;
  font-size: 3.75rem;
  color: var(--text-color);
  margin-bottom: 3rem;
  text-align: center;
}

/* === MENU LIST GRID === */
.menuList {
  width: 100%;
  max-width: var(--max-content-width);
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 3rem; /* More spacing between items */
  padding: 0 2rem;
  box-sizing: border-box;
  justify-content: center;
}

/* === MENU ITEM CARD === */
.menuItem {
  background-color: var(--card-bg-color);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  transition: transform var(--transition-speed), box-shadow var(--transition-speed);
  cursor: pointer;
  display: flex;
  flex-direction: column;
  width: 100%; /* Ensures responsive width */
  max-width: 340px; /* Prevents overly large cards on wide screens */
  margin: 0 auto; /* Center each item in its grid cell */
}

.menuItem:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px var(--hover-shadow);
}

/* === MENU ITEM IMAGE === */
.menuItem div {
  width: 100%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-bottom: 4px solid var(--text-color);
}

/* === MENU ITEM TEXT === */
.menuItem h1 {
  font-size: 1.5rem;
  color: var(--text-color);
  margin: 1rem 1.25rem 0.5rem;
  text-align: center;
}

.menuItem p {
  font-size: 1rem;
  color: var(--subtext-color);
  margin: 0 1.25rem 1.25rem;
  line-height: 1.4;
  text-align: center;
}

/* === RESPONSIVE BREAKPOINTS === */
@media (max-width: 600px) {
  .menu .menuTitle {
    font-size: 2.5rem;
  }

  .menuItem div {
    height: 180px;
  }
}
