.about {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Top Background with Overlay */
.aboutTop {
  background-size: cover;
  background-position: center;
  height: 60vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.overlay {
  background: rgba(0, 0, 0, 0.55);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.heroTitle {
  color: #fff;
  font-size: 3rem;
  text-align: center;
  font-weight: bold;
  animation: fadeInDown 1s ease-out;
}

.aboutBottom {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 4rem 2rem;
  display: flex;
  justify-content: center;
  border-radius: 16px;
}


.contentCard {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 2.5rem;
  max-width: 800px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  animation: fadeInUp 1s ease-out;
}

.contentCard h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #d35400;
  text-align: center;
}

.contentCard p {
  font-size: 1.1rem;
  line-height: 1.75;
  color: #333;
  text-align: justify;
}

/* Animations */
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive */
@media (max-width: 768px) {
  .heroTitle {
    font-size: 2rem;
    padding: 0 1rem;
  }

  .contentCard {
    padding: 2rem 1rem;
  }
}
